
import * as types from './mutation-types'
export default {

  setMoments({ commit }, moment) {
    commit(types.SET_MOMENTS, moment)
  },
  setNewExploreSign({ commit }, value) {
    commit(types.SET_NEWEXPLORESIGN, value)
  },
  setLike({ commit }, obj) {
    commit(types.SET_LIKE, obj)
  },

}



